<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul class="cart-list" v-for="cart in cartInfoList" :key="cart.id">
          <li class="cart-list-con1">
            <input
              type="checkbox"
              name="chk_list"
              :checked="cart.isChecked"
              @change="updateCart(cart)"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl" />
            <div class="item-msg">{{ cart.skuName }}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con5">
            <a
              href="javascript:void(0)"
              class="mins"
              @click="handler('minus', -1, cart)"
              >-</a
            >
            <input
              autocomplete="off"
              type="text"
              :value="cart.skuNum"
              minnum="1"
              class="itxt"
              @change="handler('change', $event.target.value * 1, cart)"
            />
            <a
              href="javascript:void(0)"
              class="plus"
              @click="handler('add', 1, cart)"
              >+</a
            >
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuPrice * cart.skuNum }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)">删除</a>
            <br />
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input
          class="chooseAll"
          type="checkbox"
          :checked="isChecked"
          @click="allChecked($event)"
        />
        <span>全选</span>
      </div>
      <div class="option">
        <a href="#none" @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择 <span>0</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ allPrice }}</i>
        </div>
        <div class="sumbtn">
          <button class="sum-btn"  @click.prevent="$router.push('/trade')">结算</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { thorottling } from "@/utils";
export default {
  name: "ShopCart",
  mounted() {
    // 初始化加载数据
    this.getData();
  },
  methods: {
    // 获取个人购物车数据
    getData() {
      this.$store.dispatch("getCarList");
    },
    //商品数量变化函数
    handler: thorottling(async function (type, disNum, cart) {
      console.log(type, disNum, cart);
      switch (type) {
        case "add":
          // 带给服务器变化的量
          disNum = 1;
          break;
        case "minus":
          // 带给服务器变化的量
          disNum = cart.skuNum > 1 ? -1 : 0;
          break;
        case "change":
          disNum =
            isNaN(disNum) || disNum < 1 ? 0 : parseInt(disNum) - cart.skuNum;
          break;
      }
      try {
        // 代表修改成功
        // 派发action
        await this.$store.dispatch("addOrUpdateShopCat", {
          skuid: cart.skuId,
          skuNum: disNum,
        });
        // 再一次获取服务器最新的数据进行展示
        this.getData();
      } catch (error) {
        console.log(new Error(error));
      }
    }),
    // 删除购物车的某一项
    deleteCartById: thorottling(async function (cart) {
      try {
        // 如果再次删除成功，就在此发请求得到数据
        await this.$store.dispatch("DeleteCartById", cart.skuId);
        // 重新刷新数据
        this.getData();
      } catch (error) {
        console.log(error.message);
      }
    }, 100),
    // 改变产品选中状态
    async updateCart(cart) {
      try {
        let checked = cart.isChecked == "0" ? "1" : "0";
        console.log(checked, 1233);
        await this.$store.dispatch("updateCartByid", {
          skuId: cart.skuId,
          isChecked: checked,
        });
        this.getData();
      } catch (error) {
        console.log(error.message);
      }
    },
    // 判断是否全部执行成功
    deleteAllCheckedCartPromise() {
      // 记录每次发送请求返回的promise对象
      let promiseAll = [];
      this.cartInfoList.forEach(async (item) => {
        if (item.isChecked == "1") {
          try {
            // 如果再次删除成功，就在此发请求得到数据
            let promise = this.$store.dispatch("DeleteCartById", item.skuId);
            promiseAll.push(promise);
          } catch (error) {
            console.log(error.message);
          }
        }
      });
      console.log(promiseAll,1231132);
      return Promise.all(promiseAll);
    },
    // 删除已选择的商品
    async deleteAllCheckedCart(){
      let res = await this.deleteAllCheckedCartPromise();
      if(res){
        this.getData();
      }
    },
    // 全选按钮
    allChecked() {
      // 如果是选定状态，就全部取消选择
      if (this.isChecked) {
        this.cartInfoList.forEach(async (item) => {
          try {
            await this.$store.dispatch("updateCartByid", {
              skuId: item.skuId,
              isChecked: '0',
            });
            this.getData();
          } catch (error) {
            console.log(error.message);
          }
        });
      }else{
        this.cartInfoList.forEach(async (item) => {
          try {
            await this.$store.dispatch("updateCartByid", {
              skuId: item.skuId,
              isChecked: '1',
            });
            this.getData();
          } catch (error) {
            console.log(error.message);
          }
        });
      }
    },
  },
  computed: {
    ...mapGetters(["cartList"]),
    cartInfoList() {
      return this.cartList.cartInfoList || [];
    },
    // 计算总价
    allPrice() {
      let res = 0;
      this.cartInfoList.forEach((item) => {
        res += item.skuNum * item.skuPrice;
      });
      return res;
    },
    // 判断底部复选框是否勾选
    isChecked() {
      return this.cartInfoList.every((item) => item.isChecked == 1);
    },
  },
};
</script>

